<template>
    <div class="edit-info">
        <h2 class="title">
            {{$route.name == '修改公号' ? '编辑公号信息' : '接管新公号'}}
        </h2>
        <Form :model="formData" class="form-group" label-position="right" :label-width="100">
            <FormItem label="公号名称" prop="name">
                <Input size="large" type="text" v-model="formData.name" placeholder="请输入公号名称"></Input>
            </FormItem>
            <FormItem label="appid" prop="appid">
                <Input size="large" type="text" v-model="formData.appid" placeholder="请输入appid"></Input>
            </FormItem>
            <FormItem label="appsecret" prop="appsecret">
                <Input size="large" type="text" v-model="formData.appsecret" placeholder="请输入appsecret"></Input>
            </FormItem>
            <FormItem label="token" prop="token">
                <Input size="large" type="text" v-model="formData.token" placeholder="请输入token"></Input>
            </FormItem>
            <FormItem>
                <Button class="edit-btn" size="large" type="primary" @click="submitForm">提交</Button>
                <Button class="edit-btn" size="large" type="error" @click="resetForm">重置</Button>
                <Button class="edit-btn" size="large" type="warning" @click="cancel">取消</Button>
            </FormItem>
        </Form>
    </div>
</template>

<script>
    export default {
        data() {
            return {
                formData: {
                    token: "mingxingshuo"
                }
            };
        },
        mounted() {
            if (this.$route.path === '/manage_test/gonghao/tuoguan/update') {
                this.formData = this.$route.params
            }
        },
        methods: {
            cancel() {
                this.$router.push('/manage_test/gonghao/tuoguan')
                this.formData = {
                    token: "mingxingshuo"
                }
            },
            resetForm() {
                this.formData = {
                    token: "mingxingshuo"
                };
            },
            submitForm() {
                if (this.$route.path === '/manage_test/gonghao/tuoguan/create') {
                    this.$axios.post('/conf/create', this.formData)
                    .then(res => {
                        window.location.href = '/manage_test/gonghao/tuoguan';
                        this.$Message.info('接管成功')
                        this.formData = {
                            token: "mingxingshuo"
                        }
                    });
                } else if(this.$route.path === '/manage_test/gonghao/tuoguan/update') {
                    this.$axios.post('/conf/update', {
                        id: this.formData._id,
                        code: this.formData.code,
                        name: this.formData.name,
                        appid: this.formData.appid,
                        appsecret: this.formData.appsecret,
                        token: this.formData.token
                    }).then(res => {
                        window.location.href = '/manage_test/gonghao/tuoguan';
                        this.$Message.info('修改成功')
                        this.formData = {
                            token: "mingxingshuo"
                        }
                    });
                }
            }
        }
    };
</script>

<style scoped>
    .edit-info {
        width: 500px;
        margin: auto;
    }
    .form-group {
      width: 500px;
    }
    .title {
      margin-bottom: 30px;
    }
    .edit-btn {
        margin-right: 10px;
    }
</style>
